<template>
  <div class="addStaff">
    <div class="addStaff_center">
      <p>
        <h1>{{title}}</h1><span class="add_span" @click="close"><Icon type="ios-close-empty" size="40"></Icon></span></p>
      <div class="add_shang">
        <div class="add_uplode" v-if="photoed == null" @click="upload">
          <img :src="photoed" />
                    上传头像
        </div>
        <div class="add_up" @click="upload" v-else>
          <img :src="photoed" width="40" height="40" />
        </div>
      </div>
      <div class="add_from">
        <strong>姓名</strong><br />
        <input type="text" v-model.trim="name" placeholder="汉字或者字母，不超过20个字符" /><br />
        <strong>简介</strong><br />
        <textarea cols="30" rows="10" v-model.trim="desc" placeholder="简短介绍下从业经历，获奖等信息，不超过46个字母符。(选填)"></textarea>
        <p style="margin-bottom: 0.1rem">
          <Tooltip content="选择服务后，该人员将在此服务预约页面内可选" placement="right">
            <strong>可服务项目</strong><Icon type="help-circled" size="18"></Icon>
          </Tooltip>
        </p>
        <Select v-model="serve" multiple style="width:3.5rem">
          <Option v-for="item in serves" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </div>
      <div class="add_button">
        <button @click="submit" v-if="!load">保存</button>
        <button  v-if="load">保存 <img src="../../../assets/bai.gif" alt="" style="width: 20px;max-height: 100%"></button>
      </div>
    </div>
    <modal ref="upload"></modal>
  </div>
</template>

<script>
  import modal from '../../upload/modal.vue'
  export default {
    data() {
      return {
        load:false,
        name: "",
        desc: "",
        serve:[],
        serves:[],
        src:'',
        title:'添加服务人员',
        flag:0,
        id:''
      }
    },
    computed: {
      photo() {
        return this.$store.getters.getPhoto;
      },
      photoed() {
        if(this.$store.getters.getPhotoed) {
          return this.$store.getters.getPhotoed.pic;
        } else {
          return this.src
        }
      }
    },
    props:["member"],
    watch:{
      member(val) {
          if(val) {
            this.id = val.id;
            this.flag = 1;
            this.title = "修改服务人员";
            this.name = val.name;
            this.desc = val.desc;
            this.src = "http://weapp.xiguawenhua.com/" + val.pic;
            this.serve = val.service_id.split(',').map( item =>{
               return (item-0);
            });
            return 1;
          }
      }
    },
    methods: {
      submit() {
        this.load = true;
        let name=[];
        this.serves.forEach( item => {
          this.serve.forEach( i => {
            if(i === item["value"]){
              name.push(item["label"])
            }
          })
        });
        if(this.flag){
//            修改
          this.post("custom/Service/updateServiceUser",{
            name: this.name,
            desc: this.desc,
            pic: `${this.photo}` || `${this.photoed.slice(29)}`,
            service_id: this.serve.toString(),
            service_name: name.toString(),
            service_user_id: this.id
          }).then( res => {
            this.load = false;
            if (res.data.code===10000) {
              this.$Modal.success({
                title: "修改成功",
                content: "成功修改服务人员"
              });
              this.$store.state.addShow = false;
            }else {
              this.$Modal.error({
                title: "修改失败",
                content: res.data.msg_test
              });
            }
          })
        }else{
//            添加
          this.post("custom/Service/createServiceUser",{
            name: this.name,
            desc: this.desc,
            pic: `${this.photo}` || `${this.photoed.slice(29)}`,
            service_id: this.serve.toString(),
            service_name: name.toString()
          }).then( res => {
            this.load = false;
            if (res.data.code===10000) {
              this.$Modal.success({
                title: "添加成功",
                content: "成功添加服务人员"
              });
              this.$store.state.addShow = false;
              this.$parent.getMemberList();
            }else {
              this.$Modal.error({
                title: "添加失败",
                content: res.data.msg_test
              });
            }
          })
        }
      },
      close() {
        this.$store.state.addShow = false;
      },
      upload() {
        this.$refs.upload.modal = true;
        this.$store.dispatch('goodsPhoto', 0);
      },
    },
    mounted(){
        this.post("custom/Service/getServiceInfo",{}).then( res => {
          if (res.data.code===10000) {
              this.serves = res.data.data;
              this.serves.map( item => {
                  item["value"] = item.id ;
                  item["label"] = item.service_name;
              })
          }
      })
    },
    components: {
      modal
    }
  }
</script>

<style scoped="scoped">
  .addStaff {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    z-index: 99;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .addStaff_center {
    padding-top: 0.2rem;
    padding-bottom: 0.4rem;
    text-align: center;
    position: relative;
    width: 4rem;
    /*height: 5.5rem;*/
    background: #FFFFFF;
    border-radius: 0.04rem;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.7);
  }

  .addStaff_center>p:nth-child(1) {
    margin: 0.2rem;
  }

  .add_span {
    position: absolute;
    top: 0.15rem;
    right: 0.3rem;
    cursor: pointer;
  }

  .add_uplode {
    display: flex;
    flex-flow: wrap;
    justify-content: center;
  }
  .add_shang{
    margin: 0.25rem auto;
    width: 0.9rem;
    height: 0.9rem;
    border-radius: 50%;
    border: 1px solid #F0F0F0;
    position: relative;
  }

  .add_uplode>img {
    width: 0.5rem;
    height: 0.5rem;
    display: block;
    margin-bottom: 0.1rem;
  }

  .add_from {
    text-align: left;
    margin-left: 0.2rem;
  }

  .add_from>strong {
    font-size: 0.14rem;
  }

  .add_from>input {
    padding-left: 0.1rem;
    border: 1px solid #c6c9cd;
    border-radius: 3px;
    color: #636972;
    width: 3.5rem;
    height: 0.35rem;
    line-height: 22px;
    margin: 0.1rem 0 0.2rem 0;
  }

  .add_from>textarea {
    padding-left: 0.1rem;
    border: 1px solid #c6c9cd;
    border-radius: 3px;
    color: #636972;
    line-height: 22px;
    width: 3.5rem;
    height: 1rem;
    margin: 0.1rem 0 0.2rem 0;
  }

  .add_button>button {
    margin-top: 0.2rem;
    width: 3.5rem;
    height: 0.4rem;
    background: #93B719;
    color: #EEFFFF;
    border: none;
    font-size: 0.16rem;
  }

  .add_up {
    position: absolute;
    top: 0;
    left: 0;
  }

  .add_up>img {
    width: 0.9rem;
    height: 0.9rem;
    border: 1px solid #F0F0F0;
    background: #F8F8F6;
    border-radius: 50%;
  }

</style>



