<template>
  <div class="staff">
    <topServer v-if="$route.name==='staff'"></topServer>
    <div class="staff_one" v-show="assign">
      <img src="../../../assets/noServerStaff.png" />
      <p>添加服务人员头像，名字，简历与所属服务</p>
      <a href="JavaScript:void(0)" @click="add">
        <i class="iconfont icon-iconjia"></i>
        <span>添加人员</span>
      </a>
    </div>
    <div class="staff_center" v-show="assignShow">
      <div class="staff_cen">
        <div>
          <a href="JavaScript:void(0)" @click="add">
            <i class="iconfont icon-iconjia"></i>
            <span>添加人员</span>
          </a>
        </div>
        <div class="add_title">
          <span>
        	  称谓设置
        	  <Tooltip placement="right" content="设置在小程序中服务人员的称谓">
                    <Icon type="help-circled" size="18"></Icon>
                 </Tooltip>
        	</span>
          <span style="display: inline-block;padding-left: 0.1rem;margin-top: 0.15rem;" v-show="linger">
        	  服务人员
        	  <span @click="addShow"><Icon type="edit" size="14" ></Icon></span>
          </span>
          <span class="staff_input" v-show="!linger">
        	  <input type="text" v-model="hashslinger"/>
        	  <span @click="addShow">
        	    <Icon type="checkmark-round" color="#93B719"></Icon>
        	  </span>
          </span>
        </div>
        <div>
        </div>
      </div>
      <div class="list">
        <table>
          <tr>
            <th></th>
            <th style="width: 14%">姓名</th>
            <th>简介</th>
            <th>所属服务</th>
            <th style="text-align:center;width: 12%;">操作</th>
          </tr>
          <tr class="td" v-for="(item,key) in data">
            <td><img :src="item.pic" style="    border-radius: 32px;height: 32px;width: 32px;"/></td>
            <td>{{item.name}}</td>
            <td class="beyond" title="">{{item.desc}}</td>
            <td>{{item.service_name}}</td>
            <td style="text-align:right;width: 12%;">
              <a href="javascript:void(0)" class="info" style="display: inline-block;margin-right: 0.08rem" @click="seeInfo(item,key)">修改</a>
              <a href="javascript:void(0)" class="info" @click="deleted(item,key)">删除</a>
            </td>
          </tr>
        </table>
        <p v-if="data.length===0" class="td_data">
          {{msg}}
        </p>
        <Page :total="total" style="margin-top: 0.2rem;" @on-change="changePage"></Page>
    </div>
    </div>
    <addStaff v-show="$store.state.addShow" :member="info"></addStaff>
  </div>
</template>

<script>
  import topServer from '../topServer.vue'
  import addStaff from './addStaff.vue'
  export default {
    data() {
      return {
        conceal: true,
        hotLine: false,
        site: false,
        assign: false,
        txt: '',
        hashslinger: '服务人员',
        linger: true,
        msg:"正在加载....",
        data:[],
        assignShow:false,
        total:0,
        info:''
      }
    },
    methods: {
       getMemberList(page) {
         this.post("custom/Service/getServiceUser" , {
           page:1 || page,
           number:10
         }).then( res => {
           if (res.data.code === 10000) {
             this.total = res.data.data.number;
             this.data = res.data.data.info;
             this.data.forEach( item => {
               item.pic ="http://weapp.xiguawenhua.com/" + item.pic;
             });

             window.frames[0].postMessage({
               tabBar:{list:this.data},
              },'*');
             if(this.data.length === 0){
               this.assign = true;
               this.assignShow = false;
             }else{
               this.assign = false;
               this.assignShow = true;
             }
           }
         })
       },
      changePage(val) {
        this.getMemberList(val);
      },
      conce() {
        this.conceal = false
      },
      add() {
        this.$store.state.addShow = true
      },
      addShow() {
        this.linger = !this.linger
      },
      seeInfo(item,key){
         this.$store.state.addShow = true;

         this.post( "custom/Service/getServiceUserById" ,{
           service_user_id: item.id
         }).then( res =>{
            if (res.data.code === 10000){
                this.info = res.data.data;
            }
         })
      },
      deleted(item,key){
//      删除
        this.$Modal.confirm({
          title: '确认删除',
          content: '<p>确认删除该服务人员</p>',
          onOk: () => {
            this.post("custom/Service/delServiceUser",{service_user_id:item.id}).then( res=> {
              if(res.data.code === 10000){
                this.data.splice(key,1);
                this.$Modal.success({
                  title: "成功删除",
                  content: "成功删除该服务人员"
                });
              }else{
                this.$Modal.error({
                  title: "删除失败",
                  content: res.data.msg_test
                });
              }
            })
          },
        });
      },
    },
     mounted() {
        this.getMemberList();
     },
    components: {
      topServer,
      addStaff
    }
  }
</script>

<style scoped="scoped">
  h1{
    font-size:0.22rem;
  }
  .staff_one {
    margin-top: 1rem;
    text-align: center;
  }

  .staff_one>img {
    width: 1.6rem;
    height: 1.6rem;
  }

  /*.staff_one>p:nth-child(2) {*/
    /*margin-top: 0.5rem;*/
    /*font-size: 0.18rem;*/
    /*font-weight: 800;*/
  /*}*/

  .staff_one>p:nth-child(2) {
    margin-top: 0.05rem;
    font-size: 0.14rem;
    margin-top: 0.05rem;
    color: #9F9F9F;
  }

  .staff_cen {
    display: flex;
  }

  .staff_one a,
  .staff_cen>div>a {
    display: inline-block;
    margin-top: 0.2rem;
    background: #59A2ED;
    padding: 0.1rem 0.2rem;
    border-radius: 0.05rem;
    /*border: 1px solid #CCCCCC;*/
    color: #fff;
  }

  .add_title {
    margin: 0.15rem 0 0 0.2rem;
  }

  .staff_input>input {
    padding-left: 0.1rem;
    border: 1px solid #c6c9cd;
    border-radius: 3px;
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, .1);
    color: #636972;
    width: 1.5rem;
    height: 0.3rem;
    line-height: 22px;
    margin: 0.1rem 0 0.2rem 0;
  }
  .list{
    margin-top: 0.2rem;
  }
  table{width: 100%;font-size: 0.14rem}
  th:nth-child(1){
    width: 0.4rem;
    background: #F1F2F3;text-align: left;
  }
  th:nth-child(2),th:nth-child(3),th:nth-child(4),th:nth-child(5){padding: 0.1rem ;background: #F1F2F3;text-align: left;}
  .td>td{  padding: 0.2rem 0.1rem;  border-bottom: 1px solid #f2f2f2;  }
  .info{  color: #576b95;display: inline-block;}
  td{white-space:nowrap; overflow:hidden; text-overflow:ellipsis;  }
  .td_data{
    width: 100%;
    padding: 0.2rem 0.1rem;
    text-align: center;
    border-bottom: 1px solid #f2f2f2;
  }
</style>



